import React, { useEffect } from "react";
import CaixaStep from "../Movimento/CaixaStep";
import Title from "../Title/index";
import Form from "react-bootstrap/Form";
const Endereco = ({ values, nextStep, prevStep, handeSubmit }) => {
  const [validated, setValidated] = React.useState(false);
  const [cep, setCep] = React.useState("");
  const [endereco, setEndereco] = React.useState({});

  useEffect(() => {
    // captura o cep digitado
    const cep = document.getElementById("cep-cadastro").value.replace("-", "");
    // usa a api do viacep para preencher os campos de endereo
    fetch(`https://viacep.com.br/ws/${cep}/json/`)
      .then(response => response.json())
      .then(data => {
        setEndereco(data);
      })

      .catch(error => {
        console.log(error);
      });
  }, [cep]);

  console.log(endereco);

  return (
    <>
      <Title
        title='Endereo'
        subtitle='Aqui voc pode registrar seu endereo.'
      />
      <Form
        noValidate
        encType='multipart/form-data'
        onChange={e => {
          // captura o foco de cada input e valida se o pattern est correto ou no adicionando a classe is-invalid ou is-valid

          if (e.target.value !== "") {
            if (e.target.validity.valid) {
              e.target.classList.remove("is-invalid");
              e.target.classList.add("is-valid");
              setValidated(false);
            } else {
              e.target.classList.remove("is-valid");
              e.target.classList.add("is-invalid");
              setValidated(true);
              // cria um elemento span para exibir a mensagem de erro com a tag invalid-feedback
              const span = document.createElement("span");
              span.classList.add("invalid-feedback");
              span.textContent = e.target.validationMessage;
            }
          }
        }}>
        <div className='mb-3'>
          <div className='form-floating mb-3'>
            <input
              type='text'
              name='cep-cadastro'
              className='form-control'
              id='cep-cadastro'
              placeholder='80123-500'
              // validacao
              required
              pattern='[0-9]{5}-[\d]{3}'
              onChange={e => {
                // cria a mascara do cep
                e.target.value = e.target.value.replace(
                  /(\d{5})(\d{3})/,
                  "$1-$2"
                );
                // captura o cep digitado
                setCep(e.target.value);
              }}
            />
            <label htmlFor='nome-cadastro'>CEP</label>
          </div>
          {validated && (
            <div className='form-text'>Por favor, insira um CEP vlido.</div>
          )}
        </div>
        {
          // verifica se endereco nao esta vazio e exibe os campos de endereco
          Object.keys(endereco).length !== 0 && (
            <>
              <div className='mb-3'>
                <div className='form-floating mb-3'>
                  <input
                    type='text'
                    name='logradouro'
                    className='form-control'
                    id='logradouro'
                    placeholder='Rua das Flores'
                    // validacao
                    required
                    pattern='[A-Za-z-0-9 ]{3,}'
                    defaultValue={endereco.logradouro}
                  />
                  <label htmlFor='logradouro'>Logradouro</label>
                </div>
                {validated && (
                  <div className='form-text'>
                    Por favor, insira um logradouro vlido.
                  </div>
                )}
              </div>

              <div className='mb-3'>
                <div className='form-floating mb-3'>
                  <input
                    type='text'
                    name='numero'
                    className='form-control'
                    id='numero'
                    placeholder='123'
                    // validacao
                    required
                    pattern='[0-9]{1,}'
                  />
                  <label htmlFor='numero'>Nmero</label>
                </div>
              </div>

              <div className='mb-3'>
                <div className='form-floating mb-3'>
                  <input
                    type='text'
                    name='bairro'
                    className='form-control'
                    id='bairro'
                    placeholder='Centro'
                    // validacao
                    required
                    pattern='[A-Za-z-0-9 ]{3,}'
                    defaultValue={endereco.bairro}
                  />
                  <label htmlFor='bairro'>Bairro</label>
                </div>
                {validated && (
                  <div className='form-text'>
                    Por favor, insira um bairro vlido.
                  </div>
                )}
              </div>
              <div className='mb-3'>
                <div className='form-floating mb-3'>
                  <input
                    type='text'
                    name='cidade'
                    className='form-control'
                    id='cidade'
                    placeholder='Curitiba'
                    // validacao
                    required
                    pattern='[A-Za-z-0-9 ]{3,}'
                    defaultValue={endereco.localidade}
                  />
                  <label htmlFor='cidade'>Cidade</label>
                </div>
                {validated && (
                  <div className='form-text'>
                    Por favor, insira uma cidade vlida.
                  </div>
                )}
              </div>

              <div className='mb-3'>
                <div className='form-floating mb-3'>
                  <select
                    className='form-select'
                    id='uf'
                    name='uf'
                    required
                    defaultValue={endereco.uf}>
                    <option value='' disabled selected>Selecione um estado</option>
                    <option value='AC'>Acre</option>
                    <option value='AL'>Alagoas</option>
                    <option value='AP'>Amap</option>
                    <option value='AM'>Amazonas</option>
                    <option value='BA'>Bahia</option>
                    <option value='CE'>Cear</option>
                    <option value='DF'>Distrito Federal</option>
                    <option value='ES'>Esprito Santo</option>
                    <option value='GO'>Gois</option>
                    <option value='MA'>Maranho</option>
                    <option value='MT'>Mato Grosso</option>
                    <option value='MS'>Mato Grosso do Sul</option>
                    <option value='MG'>Minas Gerais</option>
                    <option value='PA'>Par</option>
                    <option value='PB'>Paraba</option>
                    <option value='PR'>Paran</option>
                    <option value='PE'>Pernambuco</option>
                    <option value='PI'>Piau</option>
                    <option value='RJ'>Rio de Janeiro</option>
                    <option value='RN'>Rio Grande do Norte</option>
                    <option value='RS'>Rio Grande do Sul</option>
                    <option value='RO'>Rondnia</option>
                    <option value='RR'>Roraima</option>
                    <option value='SC'>Santa Catarina</option>
                    <option value='SP'>So Paulo</option>
                    <option value='SE'>Sergipe</option>
                    <option value='TO'>Tocantins</option>
                  </select>
                  <label htmlFor='uf'>UF</label>
                </div>
              </div>
            </>
          )
        }
      </Form>
      <CaixaStep nextStep={nextStep} prevStep={prevStep} />
    </>
  );
};

export default Endereco;
