import React from "react";
import Title from "../Title/index";
import { Form } from "react-bootstrap";
import CaixaStep from "../Movimento/CaixaStep";

const Perfil = ({ values, nextStep, prevStep, handeSubmit }) => {
  const [validated, setValidated] = React.useState(false);

  return (
    <>
      <Title
        title='Perfil'
        subtitle='Queremos saber um pouco mais sobre voc.'
      />

      <Form
        noValidate
        encType='multipart/form-data'
        onChange={e => {
          // captura o foco de cada input e valida se o pattern est correto ou no adicionando a classe is-invalid ou is-valid

          if (e.target.value !== "") {
            if (e.target.validity.valid) {
              e.target.classList.remove("is-invalid");
              e.target.classList.add("is-valid");
              setValidated(false);
            } else {
              e.target.classList.remove("is-valid");
              e.target.classList.add("is-invalid");
              setValidated(true);
              // cria um elemento span para exibir a mensagem de erro com a tag invalid-feedback
              const span = document.createElement("span");
              span.classList.add("invalid-feedback");
              span.textContent = e.target.validationMessage;
            }
          }
        }}>
        <div className='mb-3'>
          <div className='form-floating mb-3'>
            <input
              type='text'
              name='nome'
              className='form-control'
              id='nome-cadastro'
              placeholder='Johe Doe'
              // validacao
              required
              minLength={3}
              maxLength={100}
              pattern="^[a-zA-Z--0-9]+(([' -][a-zA-Z--0-9])?[a-zA-Z--0-9]*)*$"
            />
            <label htmlFor='nome-cadastro'>Nome completo</label>
          </div>
          {validated && (
            <div className='form-text'>Por favor, insira um nome vlido.</div>
          )}
        </div>
        <div className='mb-3'>
          <div className='form-floating mb-3'>
            <input
              type='email'
              name='email-cadastro'
              className='form-control'
              id='email-cadastro'
              placeholder='name@example.com'
              pattern='^[a-z0-9.]+@[a-z0-9]+\.[a-z]+(\.[a-z]+)?$'
              autoComplete='on'
            />
            <label htmlFor='email-cadastro'>E-mail</label>
          </div>
          {validated ? (
            <div className='form-text'>
              Por favor, insira um e-mail vlido.
            </div>
          ) : (
            <div className='form-text'>
              Ns nunca compartilharemos seu e-mail com mais ningum.
            </div>
          )}
        </div>
        <div className='mb-3'>
          <div className='form-floating'>
            <input
              type='password'
              name='senha-cadastro'
              className='form-control'
              id='senha-cadastro'
              placeholder='Senha'
              pattern='^(?=.*\d)(?=.*[a-z])(?=.*[A-Z])(?=.*[a-zA-Z]).{8,}$'
            />
            <label htmlFor='senha-cadastro'>Senha</label>
          </div>

          {
            // se validated for true, exibe a mensagem de erro abaixo se for false, a mensagem no  exibida e some da tela
            validated && (
              <div id='emailHelp' className='form-text'>
                A senha deve conter no mnimo 8 caracteres, uma letra maiscula,
                uma minscula e um nmero.
              </div>
            )
          }
        </div>
        <div className='mb-3 confirma-senha-usuario'>
          <div className='form-floating'>
            <input
              type='password'
              name='confirma-senha-cadastro'
              className='form-control'
              id='confirma-senha-cadastro'
              placeholder='Password'
              pattern='^(?=.*\d)(?=.*[a-z])(?=.*[A-Z])(?=.*[a-zA-Z]).{8,}$'
            />
            <label htmlFor='confirma-senha-cadastro'>Confirme a senha</label>
          </div>
        </div>
        <div className='mb-3'>
          <div className='form-floating'>
            <input
              type='text'
              name='cpf-ccadastro'
              className='form-control'
              id='cpf-cadastro'
              placeholder='CPF'
              pattern="\d{3}\.\d{3}\.\d{3}-\d{2}"
              onChange={e => {
                // cria uma mascara para formatar o CPF
                e.target.value = e.target.value
                  .replace(/\D/g, "")
                  .replace(/(\d{3})(\d)/, "$1.$2")
                  .replace(/(\d{3})(\d)/, "$1.$2")
                  .replace(/(\d{3})(\d{1,2})/, "$1-$2")
                  .replace(/(-\d{2})\d+?$/, "$1");
              }}
            />
            <label htmlFor='confirma-senha-cadastro'>CPF</label>
          </div>
        </div>
      </Form>
      <CaixaStep nextStep={nextStep} />
    </>
  );
};

export default Perfil;
